import SzButton from './components/SzButton';
import SzSimpleButton from './components/SzSimpleButton';
import { SzBiggerButton, SzSmallerButton } from './components/SzSingleButton';

export default function toolbar(szConfig) {
  const szLocaleConfig = szConfig.locale;
  return {
    buttons: {
      default: SzButton,
      bigger: SzBiggerButton,
      smaller: SzSmallerButton,
      simple: SzSimpleButton,
    },
    locale: szLocaleConfig,
  };
}
